// breakpoints
@mixin desk {
    @media (max-width: 1920px) {
        @content
    }    
}
@mixin md-desk {
    @media (max-width: 1280px) {
        @content
    }    
}
@mixin pad {
    @media (max-width: 768px) {
        @content
    }    
}
@mixin mobile {
    @media (max-width: 576px) {
        @content
    }    
}